@font-face {
  font-family: 'Ubuntu';
  src: local("Ubuntu"), url("ubuntu.woff2") format("woff2"), url("ubuntu.woff") format("woff"), url("ubuntu.ttf") format("truetype");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: 'Ubuntu';
  src: local("Ubuntu Bold"), local("Ubuntu-Bold"), url("ubuntubold.woff2") format("woff2"), url("ubuntubold.woff") format("woff"), url("ubuntubold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal; }

/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
html {
  font-size: 62.5%; }

body,
button,
input,
select,
optgroup,
textarea {
  color: #515460;
  font-family: 'Ubuntu', sans-serif;
  font-weight: 400;
  font-size: 1.4rem;
  line-height: 1.65; }

h1,
h2,
h3,
h4,
h5,
h6 {
  clear: both;
  font-family: 'Ubuntu', sans-serif;
  font-weight: 700;
  line-height: 1.17;
  margin: 0;
  margin-bottom: 2rem; }

h1 {
  font-size: 3.4rem; }

h2 {
  font-size: 2.8rem; }

h3 {
  font-size: 2.4rem; }

h4 {
  font-size: 2rem; }

h5 {
  font-size: 1.8rem; }

h6 {
  font-size: 1.6rem; }

p {
  line-height: 1.72;
  margin: 0;
  margin-bottom: 2.4rem; }

a {
  color: #515460; }

dfn,
cite,
em,
i {
  font-style: italic; }

blockquote {
  margin: 0 1.5em; }

address {
  margin: 0 0 1.5em; }

pre {
  background: #eee;
  font-family: "Courier 10 Pitch", Courier, monospace;
  font-size: 15px;
  font-size: 0.9375rem;
  line-height: 1.6;
  margin-bottom: 1.6em;
  max-width: 100%;
  overflow: auto;
  padding: 1.6em; }

code,
kbd,
tt,
var {
  font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
  font-size: 15px;
  font-size: 0.9375rem; }

abbr,
acronym {
  border-bottom: 1px dotted #666;
  cursor: help; }

mark,
ins {
  background: #fff9c0;
  text-decoration: none; }

big {
  font-size: 125%; }

a {
  color: #515460; }

a:active,
a:visited {
  color: #515460; }

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  background: #fff; }

hr {
  background-color: #ccc;
  border: 0;
  height: 1px;
  margin-bottom: 1.5em; }

ul, ol {
  margin: 0 0 1.5em 3em; }

ul {
  list-style: disc; }

ol {
  list-style: decimal; }

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: 1.5em; }

dt {
  font-weight: bold; }

dd {
  margin: 0 1.5em 1.5em; }

img {
  height: auto;
  max-width: 100%; }

figure {
  margin: 1em 0; }

table {
  margin: 0 0 1.5em;
  width: 100%; }

.container__no-padding {
  padding-left: 0;
  padding-right: 0; }

.no-gutters {
  margin-right: 0;
  margin-left: 0; }
  .no-gutters > .col,
  .no-gutters > [class*="col-"] {
    padding-right: 0;
    padding-left: 0; }

/* theme icons */
.li-icon {
  content: '';
  height: 15px;
  width: 15px;
  display: inline-block;
  background-repeat: no-repeat;
  background-size: 15px;
  transition: background-image .2s ease;
  cursor: pointer; }

.li-youtube_w {
  background-image: url("../images/icons/youtube_w.svg"); }

.li-youtube:hover {
  background-image: url("../images/icons/youtube_h.svg"); }

.li-instagram_w {
  background-image: url("../images/icons/instagram_w.svg"); }

.li-instagram:hover {
  background-image: url("../images/icons/instagram_h.svg"); }

.li-facebook_w {
  background-image: url("../images/icons/facebook_w.svg"); }

.li-facebook,
.li-facebook_w,
.li-facebook_h {
  background-size: 15px 17px; }

.li-facebook:hover {
  background-image: url("../images/icons/facebook_h.svg"); }

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
:focus {
  outline: 0; }

input[type="button"],
input[type="reset"],
input[type="submit"] {
  border: 1px solid;
  border-color: #ccc #ccc #bbb;
  border-radius: 3px;
  background: #e6e6e6;
  color: rgba(0, 0, 0, 0.8);
  font-size: 1rem;
  line-height: 1;
  padding: .6em 1em .4em; }

input[type="button"]:hover,
input[type="reset"]:hover,
input[type="submit"]:hover {
  border-color: #ccc #bbb #aaa; }

input[type="button"]:active,
input[type="button"]:focus,
input[type="reset"]:active,
input[type="reset"]:focus,
input[type="submit"]:active,
input[type="submit"]:focus {
  border-color: #aaa #bbb #bbb; }

.input-text,
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
  color: #666;
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 3px; }

input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="number"]:focus,
input[type="tel"]:focus,
input[type="range"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="week"]:focus,
input[type="time"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="color"]:focus,
textarea:focus {
  color: #111; }

select {
  border: 1px solid #ccc; }

textarea {
  width: 100%; }

/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/
.text-block {
  padding: 38px 15px;
  margin-bottom: 58px; }

.text-block__image {
  margin: 14px -15px;
  max-width: calc(100% + 30px);
  position: relative;
  z-index: 101; }

